import '../css/components.css';
import v1 from '../img/demo/6.png';
import v2 from '../img/demo/7.png';
import v3 from '../img/demo/8.png';
import v4 from '../img/demo/9.png'; 

export default function LiveAnyWhere() {
    return (

        <>

            {/* desktop version  */}
            <div className="container">
                <div className="heading">Live Any Where</div>

                <div className="LiveAnyWhereCont">

                    {/* map iterator here */}
                    <div className="LiveAnyWhereSin">
                        <img src={v1} alt="" />
                        <p>Entire homes</p>
                    </div>
                    <div className="LiveAnyWhereSin">
                        <img src={v2} alt="" />
                        <p>Cabin and cottages</p>
                    </div>
                    <div className="LiveAnyWhereSin">
                        <img src={v3} alt="" />
                        <p>Pets welcome</p>
                    </div>
                    <div className="LiveAnyWhereSin">
                        <img src={v4} alt="" />
                        <p>Unique stays</p>
                    </div>

                </div>

            </div>

        </>

    );
}